import React from 'react';
import NewWebApp from '../../images/new-web-app.png';
import ToFtp from '../../images/to-ftp.png';
import ReadyWebsite from '../../images/ready-website.png';

export default class Thanks extends React.Component {
	render() {
		return <section className="wrap-content row">
			<h2>Dziki za wysuchanie!</h2>
			<p>Byo mi niezmiernie mio wprowadzi Was do wiata chmury publicznej. Licz, e wycignlicie z tej prezentacji ile to moliwe. Dla przypomnienia poniej podsumowanie najwaniejszych rzeczy z prezentacji. Niej znajedziecie te linki do materiaw z sieci, ktre pomog Wam zacz z chmur publiczn.</p>
			<p>Cze!</p>
			<p>Kuba</p>
			<h3>Podsumowanie prezentacji</h3>
			<ul>
				<li>Umiejtno korzystania z chmury publicznej, wrd programistw, jest czsto mile widziana przez pracodawcw. Korzysta z niej wiele firm w naszym regionie i na caym wiecie. Chmura publiczna dostarczana jest przez m. in. Microsoft, Google, Amazon. Niezalenie od "gazi" programowania, ktry Ci interesuje - znajomo chmury publicznej zawsze bdzie przydatna.</li>
				<li>Zrobienie pierwszego kroku do nabycia tego skilla jest bardzo proste. Aby zacz wystarczy prosta stronka jak ta i przejcie 3 szybkich krokw:
					<div className="row centered">
						<div className="col-md-4 col-md-offset-4">
							<div className="thumbnail">
								<img src={NewWebApp} alt="New Web App" />
								<div className="caption">
								<p>Tworzymy now stron na Azure</p>
							</div>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="col-md-4 col-md-offset-4">
							<div className="thumbnail">
								<img src={ToFtp} alt="To Ftp" />
								<div className="caption">
								<p>Wrzucamy na FTP</p>
							</div>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="col-md-4 col-md-offset-4">
							<div className="thumbnail">
								<img src={ReadyWebsite} alt="Strona Gotowa" />
								<div className="caption">
								<p>Strona gotowa</p>
							</div>
							</div>
						</div>
					</div>
				</li>
				<li>Pamitacie jak mwiem Wam, e chmura oszczedzi Wam mas czasu? Wiadomo, e my programici wolimy programowa, a nie konfigurowa i uera si z infrastruktur. Chmura publiczna to usuga, ktra po prostu dziaa, a jak nie dziaa to nie Twj problem. Wyprbuj przy swoim nastepnym projekcie zaliczeniowym i zobacz jak twiej i szybciej si tworzy aplikacje.</li>
			</ul>
		</section>
	}
}


